@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa&family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.info-container {
    width: 100%;
    height: 28px;
    display: flex;
    flex-direction: row;
}
.info-container img {
    width: 28px;
    height: 28px;
}
.info-container .nom {
    display: flex;
    flex-direction: column;
    padding-left: 8px;
}
.info-container .nom p {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 15px;
    display: flex;
    align-items: center;
    color: #000000;
}
.info-container .nom span {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 11px;
    line-height: 13px;
    display: flex;
    align-items: center;
    color: rgba(0, 0, 0, 0.8);
}
* {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}
section {
    display: flex;
    flex-direction: column;
}
.titre {
    background: url(/ReactApp/static/media/background.f652b76e.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 707px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
/* STATUS BAR */
.statusbar {
    height: 44px;
}




/* ACCUEIL */
/* titre */
.titre .titre-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 220px;
    height: 54px;
}
.titre .titre-container img {
    width: 38px;
    height: 38px;
    margin-right: 25px;
}
.titre .titre-container p {
    font-family: Comfortaa;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    line-height: 54px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: -0.015em;
    color: #000000;
}
/* author */
.author-container {
    width: 100%;
    height: 28px;
    padding: 0 0 20px 40px;
}
/* button login / register */
.connection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100px;
}
.connection .button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100px;
}
/* register */
.connection .right {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
}
.connection .right a {
    width: 167px;
    height: 52px;
    background: #000000;
    border-radius: 6px;
    cursor: pointer;
    text-decoration: none;
}
.connection .right a span {
    color: white;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
}
/* login */
.connection .left {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    height: 100px;
}
.connection .left a {
    width: 167px;
    height: 52px;
    background: #FFFFFF;
    border: 2px solid #000000;
    box-sizing: border-box;
    border-radius: 6px;
    cursor: pointer;
    text-decoration: none;
}
.connection .left a span {
    color: black;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
}





/* CONNECTION / REGISTER DESIGN */
#connection {
    display: flex;
    flex-direction: column;
    align-items: center;
}
/* back button */
.back {
    width: 343px;
    height: 52px;
}
.fa-chevron-left {
    color: black;
    height: 22px;
}
/* title */
.connection-title {
    width: 343px;
    height: 52px;
    font-family: Comfortaa;
}
/* terms of service */
.privacy {
    width: 343px;
    height: 52px;
    padding-top: 32px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 18px;
    color: #000000;
}
.privacy span {
    text-decoration: underline;
}



/* DISCOVER */
/* news */
#news {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
}
#news .statusbar {
    height: 38px;
}
.title {
    width: 343px;
    height: auto;
    padding-bottom: 20px;
}
.title h1 {
    font-family: Comfortaa;
    font-style: normal;
    font-weight: normal;
    font-size: 25px;
    line-height: 40px;
    display: flex;
    align-items: center;
    letter-spacing: -0.015em;
    color: #000000;
}
.news-posts {
    width: 343px;
    height: auto;
    padding-bottom: 20px;
}
.news-posts p {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 15px;
    display: flex;
    align-items: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #000000;
}
/* all */
.title p {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 13px;
    line-height: 15px;
    display: flex;
    align-items: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #000000;
}
/* button */
.see-more {
    width: 343px;
    height: 52px;
    background: #FFFFFF;
    border: 2px solid #000000;
    box-sizing: border-box;
    border-radius: 6px;
    margin-top: 20px;
    margin-bottom: 95px;
    cursor: pointer;
}
.see-more span {
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 13px;
    line-height: 15px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #000000;
    display: flex;
    justify-content: center;
}
.see-more a {
    text-decoration: none;
} 
/* images en full screen */
.imageFullScreen-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
}
.High {
    width: 375px;
    height: 812px;
    position: relative;
}
.profilFullScreen-container {
    width: 343px;
    padding: 36px 16px 0 16px;
    position: absolute;
    height: 28px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.profilFullScreen-container .quit {
    width: 10.71px;
    height: 10.71px;
}
.profilFullScreen-container a {
    width: 10.71px;
    height: 10.71px;
    display: flex;
    align-items: center;
}
.imageFullScreen-container .nom p {
    color: white;
}
.imageFullScreen-container .nom span {
    color: white;
}




/* SEARCH */
#search {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
}
#hide {
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;
}

.contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.input-1 {
    width: 343px;
    height: 72px;
}
.text {
    width: 343px;
    height: 52px;
    padding-left: 10px;
    margin-bottom: 10px;
    background: #FFFFFF;
    border: 2px solid #000000;
    box-sizing: border-box;
    font-family: Roboto;
    font-size: 15px;
    color: #000000;
}


.button-1 {
    width: 343px;
    height: 52px;
    text-decoration: none;
}
.submit {
    width: 343px;
    height: 52px;
    background: #000000;
    border: 2px solid #000000;
    box-sizing: border-box;
    border-radius: 6px;
    font-family: Roboto;
    font-weight: 900;
    font-size: 13px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    cursor: pointer;
}
.submit span {
    color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: center;
}
.media-container {
    display: flex;
    flex-direction: row;
    width: 343px;
    height: 440px;
}
.post-container {
    height: 393px;
}
.profil-container {
    padding-top: 16px;
    height: 28px;;
}
img {
    width: 343px;
    height: 343px;
}

.image-container {
    display: flex;
    flex-direction: row;
    width: 343px;
    height: auto;
    padding-bottom: 20px;
}
.image-left {
    display: flex;
    flex-direction: column;
    margin-right: 8px;
}
.image-right {
    display: flex;
    flex-direction: column;
}
.small-image {
    width: 167px;
    height: 220px;
    margin-bottom: 8px;
}
.high-image {
    width: 167px;
    height: 310px;
    margin-bottom: 8px;
}

nav {
    width: 343px;
    height: 83px;
    background: #FFFFFF;
    box-shadow: 0px -0.5px 0px rgba(0, 0, 0, 0.3);
    position: fixed;
    bottom: 0%;
}
nav ul {
    display: flex;
    flex-direction: row;
}
nav ul li {
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.nav-li-size {
    width: 15.56px;
    height: 14.06px;
}
.add-container {
    background: linear-gradient(202.17deg, #FF00D6 8.58%, #FF4D00 91.42%);
    border-radius: 20px;
    height: 40px;
    width: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.adjust {
    display: flex;
    justify-content: center;
    align-items: center;
}
.imageSearch-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 343px;
    height: auto;
    flex-wrap: wrap;
}
.imageSearch-container :nth-child(1), 
.imageSearch-container :nth-child(2),
.imageSearch-container :nth-child(4),
.imageSearch-container :nth-child(5),
.imageSearch-container :nth-child(7),
.imageSearch-container :nth-child(8),
.imageSearch-container :nth-child(10),
.imageSearch-container :nth-child(11),
.imageSearch-container :nth-child(13),
.imageSearch-container :nth-child(14) {
    margin-right: 8px;
}
.dog-size {
    width: 107px;
    height: 107px;
    margin-bottom: 8px;
}
